import women from '../../../source/images/main/womenWary.png'
import strelka from '../../../source/images/index/strelka_big.svg'
import './styles/banner.scss';
import { decrement, increment } from '../../header/modalReduser'
import { useSelector, useDispatch } from 'react-redux'
import { useCallback } from 'react';
export default function Banner(){
    const dispatch = useDispatch() 
    return(
        <>
            <div className="banner wow animate__animated animate__fadeInUp">
                <div className="banner_content">
                    <div className="banner_text">
                        <h4>
                          <br/>  !
                        </h4>
                        <p>    : <br/>  ,    <br/>,    <br/> .   !</p>
                    </div>
                    <div className="banner_images">
                        <img src={women} alt="" />
                        <p>      <br/>MaryPro School </p>
                    </div>
                </div>
                <a onClick={() => dispatch(increment())}>    <img src={strelka} alt="" /></a>
            </div>
        </>
    )
}